import React, { useState, useCallback, useEffect } from "react";
import * as Our from "../components/Core";
import Header from "../components/Header";
import Spinner from "../components/Spinner";
import SeedCard from "../components/SeedCard";
import { getGeohashRange } from "../utils";
import enums from "../constants/enums";
import { useSession, auth } from "../services/firebase";
import { useHistory, useLocation } from "react-router";
import { useQuery, gql, useMutation } from "@apollo/client";
import mapboxgl from "mapbox-gl/dist/mapbox-gl.js";
import MapSearchInput from "../components/MapSearchInput";

mapboxgl.accessToken =
  "pk.eyJ1IjoibGVpZnJpa3NoZWltIiwiYSI6ImNrYThza2VibzAyd3cycXBqZjRpNjdlc2MifQ.ohHSpM7z_MU_NLEsJ1r2lQ";

function useRouteQuery() {
  return new URLSearchParams(useLocation().search);
}

const ME = gql`
  query Me {
    me {
      bookmarkedSeeds {
        id
      }
    }
  }
`;

const SEEDS = gql`
  query Seeds($filter: SeedFilters!) {
    seeds(filter: $filter) {
      id
      name
      place {
        lat
        lng
        address
      }
    }
  }
`;

const ADD_SEED_BOOKMARK = gql`
  mutation addSeedBookmark($id: ID!) {
    addSeedBookmark(id: $id) {
      id
    }
  }
`;

const REMOVE_SEED_BOOKMARK = gql`
  mutation removeSeedBookmark($id: ID!) {
    removeSeedBookmark(id: $id) {
      id
    }
  }
`;

export default function SearchResults() {
  const userSession = useSession();
  const query = useRouteQuery();
  const history = useHistory("");

  const [mapInstance, setMapInstance] = useState(null);

  const lat = parseFloat(query.get("lat"));
  const lng = parseFloat(query.get("lng"));
  const geoHashRange = getGeohashRange(lat, lng);

  const [showFilters, setShowFilters] = useState(false);
  const [showPlaceSearch, setShowPlaceSearch] = useState(false);

  const [addBookmark] = useMutation(ADD_SEED_BOOKMARK);
  const [removeBookmark] = useMutation(REMOVE_SEED_BOOKMARK);

  const { data: myData } = useQuery(ME);

  const { data: seedData, loading: loadingSeeds } = useQuery(SEEDS, {
    variables: {
      filter: {
        AND: [
          {
            geohash: {
              gte: geoHashRange.lower,
            },
          },
          {
            geohash: {
              lte: geoHashRange.upper,
            },
          },
        ],
      },
    },
  });

  function handleSelectPlace(place) {
    history.push(`/results?&lat=${place.lat}&lng=${place.lng}`);
  }

  function onMapLoad() {
    //  this is map instance
    this.resize();
  }

  function onMapMove() {
    // this is map instance
    const mapLng = this.getCenter().lng.toFixed(4);
    const mapLat = this.getCenter().lat.toFixed(4);
    const mapZoom = this.getZoom().toFixed(2);
    // TODO: Handle move map but don't search until asked
    //history.push(`/results?&lat=${mapLat}&lng=${mapLng}`);
  }

  const mapRef = useCallback((node) => {
    if (node !== null) {
      const map = new mapboxgl.Map({
        container: node,
        style: "mapbox://styles/mapbox/streets-v11",
        center: [lng, lat],
        zoom: 10,
      });
      setMapInstance(map);
      map.on("load", onMapLoad);
      map.on("moveend", onMapMove);
    }
    return () => {
      mapInstance.off("load", onMapLoad);
      mapInstance.off("moveend", onMapMove);
    };
  }, []);

  useEffect(() => {
    if (mapInstance) {
      mapInstance.setZoom(10);
      mapInstance.setCenter([lng, lat]);
    }
  }, [lat, lng]);

  useEffect(() => {
    if (seedData && mapInstance) {
      seedData.seeds.map((seed) => {
        const marker = new mapboxgl.Marker()
          .setLngLat([seed.place.lng, seed.place.lat])
          .addTo(mapInstance);
      });
    }
  }, [JSON.stringify(seedData), mapInstance]);

  return (
    <>
      <Header user={userSession} onSignOut={() => auth.signOut()}></Header>

      <Our.Container center size="full">
        <>
          <Our.Grid gap="sm">
            <Our.GridItem sm="12" md="4">
              <Our.Box padding-t="md" padding-x="lg">
                <Our.Box inline margin-r="sm">
                  <Our.Button
                    rounded
                    size="sm"
                    type="secondary"
                    onClick={() => setShowPlaceSearch(!showPlaceSearch)}
                  >
                    Change location
                    <i
                      style={{ "--ggs": 0.6 }}
                      className="gg-pin"
                      slot="end"
                    ></i>
                  </Our.Button>
                </Our.Box>
                <Our.Box inline margin-r="sm">
                  <Our.Button
                    rounded
                    size="sm"
                    type="secondary"
                    onClick={() => setShowFilters(!showFilters)}
                  >
                    Filters
                    <i
                      style={{ "--ggs": 0.6 }}
                      className="gg-options"
                      slot="end"
                    ></i>
                  </Our.Button>
                </Our.Box>

                <Our.Box margin-y="lg">
                  {loadingSeeds ? (
                    <Our.Flex justify-content="center" align-items="center">
                      <Spinner></Spinner>
                    </Our.Flex>
                  ) : seedData?.seeds.length ? (
                    seedData.seeds.map((seed) => {
                      const isBookmarked = myData?.me.bookmarkedSeeds.some(
                        (s) => s.id === seed.id
                      );
                      return (
                        <SeedCard
                          isAuthenticated={userSession.uid}
                          isBookmarked={isBookmarked}
                          onRemoveBookmark={() => {
                            removeBookmark({
                              variables: {
                                id: seed.id,
                              },
                              optimisticResponse: {
                                __typename: "Mutation",
                                removeSeedBookmark: {
                                  id: seed.id,
                                  __typename: "Seed",
                                },
                              },
                              update(cache, { data: { removeSeedBookmark } }) {
                                const { me } = cache.readQuery({
                                  query: ME,
                                  variables: { id: userSession.uid },
                                });
                                cache.writeQuery({
                                  query: ME,
                                  variables: { id: userSession.uid },
                                  data: {
                                    me: {
                                      ...me,
                                      bookmarkedSeeds: me.bookmarkedSeeds.filter(
                                        (b) => b.id !== removeSeedBookmark.id
                                      ),
                                    },
                                  },
                                });
                              },
                            });
                          }}
                          onAddBookmark={() => {
                            addBookmark({
                              variables: {
                                id: seed.id,
                              },
                              optimisticResponse: {
                                __typename: "Mutation",
                                addSeedBookmark: {
                                  id: seed.id,
                                  __typename: "Seed",
                                },
                              },
                              update(cache, { data: { addSeedBookmark } }) {
                                const { me } = cache.readQuery({
                                  query: ME,
                                });
                                cache.writeQuery({
                                  query: ME,
                                  data: {
                                    me: {
                                      ...me,
                                      bookmarkedSeeds: [
                                        ...me.bookmarkedSeeds,
                                        { ...addSeedBookmark },
                                      ],
                                    },
                                  },
                                });
                              },
                            });
                          }}
                          key={seed.id}
                          seed={seed}
                        ></SeedCard>
                      );
                    })
                  ) : (
                    <Our.Text tag="h2" weight="400">
                      No seeds found
                    </Our.Text>
                  )}
                </Our.Box>
              </Our.Box>
            </Our.GridItem>
            <Our.GridItem sm="none" md="8">
              <div ref={mapRef}></div>
            </Our.GridItem>
          </Our.Grid>
        </>
      </Our.Container>
      <Our.Modal
        open={showPlaceSearch}
        onToggle={(e) => setShowPlaceSearch(e.target.open)}
        title="Search for place"
      >
        <MapSearchInput
          onSelect={(place) => {
            handleSelectPlace(place);
            setShowPlaceSearch(false);
          }}
        ></MapSearchInput>
      </Our.Modal>
      <Our.Modal
        title="Filters"
        open={showFilters}
        onToggle={(e) => setShowFilters(e.target.open)}
      >
        <Filters></Filters>
      </Our.Modal>
    </>
  );
}

function Filters() {
  return (
    <>
      <Our.Box margin-y="md">
        <Our.Label>Sun conditions</Our.Label>
        <Our.Select full>
          {enums.sunConditions.map((option) => (
            <Our.Option key={option} value={option}></Our.Option>
          ))}
        </Our.Select>
      </Our.Box>
      <Our.Box margin-y="md">
        <Our.Label>Soil conditions</Our.Label>
        <Our.Select full>
          {enums.soilConditions.map((option) => (
            <Our.Option key={option} value={option}></Our.Option>
          ))}
        </Our.Select>
      </Our.Box>
      <Our.Box margin-y="md">
        <Our.Label>Water requirement</Our.Label>
        <Our.Range
          full
          labels="Low,Medium,High"
          sensitivity="0.7"
          value="0,4"
          max="4"
          min="0"
          step="1"
        ></Our.Range>
      </Our.Box>
      <Our.Box margin-y="md">
        <Our.Label>Temperature</Our.Label>
        <Our.Range
          full
          labels="0,10,20,30,40"
          sensitivity="0.7"
          value="0,40"
          max="40"
          min="-0"
          step="5"
        ></Our.Range>
      </Our.Box>
      <Our.Box margin-y="md">
        <Our.Label>What are you looking for?</Our.Label>
        {enums.seedCategories.map((option) => (
          <Our.Checkbox key={option} size="sm" value={option}>
            {option}
          </Our.Checkbox>
        ))}
      </Our.Box>
      <Our.Box margin-y="md">
        <Our.Button full type="primary">
          Apply filters
        </Our.Button>
      </Our.Box>
    </>
  );
}
