import React, { useState } from "react";
import { useSession, auth } from "../services/firebase";

import * as Our from "../components/Core";
import Header from "../components/Header";
import enums from "../constants/enums";
import MapSearchInput from "../components/MapSearchInput";
import { useHistory } from "react-router-dom";

export default function Home() {
  const user = useSession();
  const history = useHistory();

  const [isFindingLocation, setIsFindingLocation] = useState(false);

  function handleSelectPlace(place) {
    history.push(`/results?&lat=${place.lat}&lng=${place.lng}`);
  }

  function findMe() {
    if (navigator.geolocation) {
      setIsFindingLocation(true);
      navigator.geolocation.getCurrentPosition((pos) => {
        setIsFindingLocation(false);
        const lat = pos.coords.latitude;
        const lng = pos.coords.longitude;
        handleSelectPlace({ lat, lng });
      });
    } else {
      console.log("Geolocation is not supported by this browser.");
    }
  }

  return (
    <div id="home">
      <Header
        user={user}
        onSignOut={() => {
          auth.signOut();
        }}
      ></Header>

      <Our.Box padding-x="lg" margin-y="xl">
        <Our.Container center size="lg">
          <Our.Text
            inline
            className="main-heading"
            weight="400"
            look=""
            tag="h1"
          >
            Find seeds that grow in your climate
          </Our.Text>
        </Our.Container>
      </Our.Box>

      <Our.Container center size="lg">
        <Our.Container size="sm">
          <Our.Box depth="sm" padding="md">
            <Our.Grid gap="sm" columns="9">
              <Our.GridItem sm="9" md="4">
                <MapSearchInput
                  size="xl"
                  placeholder="Search for place"
                  onSelect={(place) => {
                    handleSelectPlace(place);
                  }}
                ></MapSearchInput>
              </Our.GridItem>
              <Our.GridItem sm="none" md="1">
                <Our.Flex
                  full-height
                  align-items="center"
                  justify-content="center"
                >
                  or
                </Our.Flex>
              </Our.GridItem>
              <Our.GridItem sm="none" md="4">
                <Our.Button
                  onClick={() => findMe()}
                  full
                  size="xl"
                  type="primary"
                >
                  <i
                    slot="start"
                    className={isFindingLocation ? "gg-spinner" : "gg-pin"}
                  ></i>
                  Find me
                </Our.Button>
              </Our.GridItem>
            </Our.Grid>
          </Our.Box>
        </Our.Container>
      </Our.Container>

      <Our.Box padding-x="lg" margin-y="xl">
        <Our.Container size="lg" center>
          <Our.Box margin-y="lg">
            <Our.Text weight="400" tag="h1">
              Explore
            </Our.Text>
          </Our.Box>
          <Our.Box margin-y="lg">
            <Our.Grid columns="12">
              {enums.seedCategories.map((cat) => {
                return (
                  <Our.GridItem sm="6" md="3">
                    <Our.Box padding="lg" depth="md" clickable>
                      <Our.Text>{cat}</Our.Text>
                    </Our.Box>
                  </Our.GridItem>
                );
              })}
            </Our.Grid>
          </Our.Box>
        </Our.Container>
      </Our.Box>
    </div>
  );
}
